import React, { Component } from 'react';
import phone from "./mix2.jpg";
import {Stitch, RemoteMongoClient, AnonymousCredential} from 'mongodb-stitch-browser-sdk';


import './App.css';

import Button from "@material-ui/core/Button"
import LinearProgress from "@material-ui/core/LinearProgress";
import TextField from "@material-ui/core/TextField";
import { withStyles } from "@material-ui/core";

require('dotenv').config();

const price = 370;

const client = Stitch.initializeDefaultAppClient('basic-progress-bar-xluvs');
client.auth.loginWithCredential(new AnonymousCredential());
const db = client.getServiceClient(RemoteMongoClient.factory, 'mongodb-atlas').db('bar');
const collection = db.collection("current");




const styles = {
  bar: {
    width: 200,
    marginRight:50,
    margin:"auto"
  },
  barDiv: {
    marginTop:100,
    display:"flex",
    flexDirection:"row"
  },
  textDiv: {
    marginTop:50,
    display:"flex",
    flexDirection:"row"
  },

}

class App extends Component {
  client;
  mongodb;
  constructor(props) {
    super(props);

    collection.find().first()
    .then(result => {this.setState({current: result.current})});

    
    
    
    this.state = {
      current: 0,
      increment: '',
    };
  }

  componentDidMount() {

    

  }

  getValue= () => {
    collection.find().first()
    .then(result => this.setState({current: result.current}));
  }


  handleIncrementChange = () => event => {
    this.setState({increment: event.target.value});
  }

  handleCurrentChange = () => {
    this.getValue();
    const value = this.state.current*1+this.state.increment*1;
    if (value !== 0) {
      collection.updateOne({id: 0}, {$set:{current:value}})
      .then(result=> this.getValue());
    }
    this.setState({increment:''});
  }

  

  render() {
    
    const {classes} = this.props;
    const {current, increment} = this.state;
    return (
      <div className="App">
        <header className="App-header">
          <img src={phone} className="App-logo" alt="logo" />
          <div className={classes.barDiv}>
            <LinearProgress className={classes.bar} variant="determinate"  value={100*current/price} />
            ${current}/${price}
          </div>

          <div className={classes.textDiv}>

            <TextField
              value={increment}
              id="standard-number"
              label="Enter amount"
              type="number"
              margin="normal"
              onChange= {this.handleIncrementChange()}
            />

            <Button onClick={() => this.handleCurrentChange()}>Add amount</Button>
          </div>
          
        </header>
      </div>
    );
  }
}

export default withStyles(styles)(App);